import React from "react";
import {
  Grid,
  Typography,
  Table,
  TableBody,
  TableRow,
  TableCell,
  Box
} from "@material-ui/core";
import { makeStyles, createStyles, Theme } from "@material-ui/core/styles";
import Tab from "./Tab";
import { rootSmXl, paddingDownSm } from "../helper/DefaultStyle";
import ExpansionPanel from "./ExpansionPanel";
import ReactGA from "react-ga";

const useStylesTarifs = makeStyles((theme: Theme) =>
  createStyles({
    [theme.breakpoints.down("sm")]: {
      th: {
        fontStyle: "normal",
        fontWeight: "normal",
        fontSize: 10,
        color: "#898989"
      },
      td: {
        fontStyle: "normal",
        fontWeight: "normal",
        fontSize: 10,
        color: "black"
      }
    },
    [theme.breakpoints.between("sm", "xl")]: {
      th: {
        fontStyle: "normal",
        fontWeight: "normal",
        fontSize: 16,
        color: "#898989"
      },
      td: {
        fontStyle: "normal",
        fontWeight: "normal",
        fontSize: 16,
        color: "black"
      }
    }
  })
);

const Tarifs = () => {
  const classes = useStylesTarifs({});

  const rows = [
    { code: "   ", value: "" },
    { code: " ", value: "" },
    { code: "-", value: "" },
    { code: "", value: "" },
    { code: "  ", value: "" },
    {
      code: "  ",
      value: (
        <Typography className={classes.td}>
           -    <br /> 1%  . -   
          
        </Typography>
      )
    },
    {
      code: " ",
      value:
        " -    300 000   .    "
    }
  ];

  return (
    <Box width={0.7}>
      <Table>
        <TableBody>
          {rows.map((m, i) => (
            <TableRow key={i}>
              <TableCell className={classes.th}>{m.code}</TableCell>
              <TableCell className={classes.td}>{m.value}</TableCell>
            </TableRow>
          ))}
          <TableRow key={15}>
            <TableCell colSpan={2} className={classes.td}>
              {
                <span>
                  <img src="pdf.svg" alt="pdf" />
                  &nbsp;  .pdf
                </span>
              }
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </Box>
  );
};

const Conditions = () => {
  const classes = useStylesTarifs({});

  const rows = [
    { code: " ", value: " 3 000 000 " },
    { code: " ", value: " 55 " },
    {
      code: "  ( )",
      value: (
        <Typography className={classes.td}>
          <b>0%   </b>
          <br />
          22,99% -     (  26%)
          <br />
          24,99% -    (  29%)
        </Typography>
      )
    },
    { code: "  ( )", value: "0%" },
    { code: "", value: " 30%" }
  ];

  return (
    <Box width={0.7}>
      <Table>
        <TableBody>
          {rows.map((m, i) => (
            <TableRow key={i}>
              <TableCell className={classes.th}>{m.code}</TableCell>
              <TableCell className={classes.td}>{m.value}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Box>
  );
};

const Documents = () => {
  const classes = useStylesTarifs({});

  const rows = [" .pdf", " .pdf"];

  return (
    <Box width={0.7}>
      <Table>
        <TableBody>
          {rows.map((m, i) => (
            <TableRow key={i}>
              <TableCell colSpan={2} className={classes.td}>
                {
                  <Typography>
                    <img src="pdf.svg" alt="pdf" />
                      &nbsp; {m}
                  </Typography>
                }
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Box>
  );
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    [theme.breakpoints.down("sm")]: {
      root: {
        padding: paddingDownSm
      },
      title: {
        fontStyle: "normal",
        fontWeight: "bold",
        fontSize: 20,
        color: "#141414"
      }
    },
    [theme.breakpoints.between("sm", "xl")]: {
      ...rootSmXl,
      title: {
        fontStyle: "normal",
        fontWeight: "bold",
        fontSize: 40,
        color: "#141414"
      }
    }
  })
);

const AdditionalInfo = () => {
  const classes = useStyles({});

  const swipeTab = (index: number) => {
    ReactGA.event({
      category: "BccCard",
      action: index === 0 ? "Additional_Rates" : "Additionally_FAQ"
    });
  };

  return (
    <Grid container className={classes.root}>
      <Grid item xl={12} lg={12} md={12} sm={12} xs={12}>
        <Typography className={classes.title}></Typography>
      </Grid>
      <Grid item xl={12} lg={12} md={12} sm={12} xs={12}>
        <Tab
          onHandleChanged={(i: number) => swipeTab(i)}
          menuTitle={[
            "",
            "",
            "  ",
            ""
          ]}
          pans={[<Conditions />, <Tarifs />, <ExpansionPanel />, <Documents />]}
        />
      </Grid>
    </Grid>
  );
};

export default AdditionalInfo;
